html {
	background: #2b2b40;
}

html, body {
	margin: 0;
	padding: 0;
}

body {
	//font-family: 'Nanum Pen Script', cursive;
	font-family: 'Roboto Condensed', sans-serif;
}

a {
	color: #00a4ca;
	text-decoration: none;
}

a:hover {
	text-decoration: underline;
}

img {
	max-width: 100%;
}

h1 strong, h2 strong {
	font-weight: 700;
}

h1 {
	font-weight: 300;
	font-size: 3.8em;
	margin: 0;
}

h1.new {
	font-weight: 300;
	font-size: 3em;
	margin: 0;
	color: $primary-color;
	border-bottom:1px solid $primary-color;
	width:100%;
	padding-bottom:3px;
}

h2 {
	font-weight: 300;
	font-size: 3em;
	margin: 0 0 30px 0;
	line-height: 1.2em;
}

h3 {
	margin: 20px 0 10px 0;
}

h2 a {
	color: #000;

	&:hover {
		text-decoration: none;
	}
}

p, address, ul {
	font-size: 1.34em;
	margin-bottom: 20px;
	font-weight: 300;
	line-height: 1.6em;
	font-family: 'Roboto Condensed', sans-serif;
	//font-family: 'Nanum Pen Script', cursive;
}

p strong {
	font-weight: 600;
}

audio {
	width: 100%;
	max-width:600px;
}
