.container, .text-container {
	margin: 0 auto;
	position: relative;
	padding: 0 20px;
}

.text-container {
	max-width: 750px;
}

.container {
	max-width: 1140px;
}

.light-logo {
	align-items: center;
	display: inline-flex;
}

.dark-logo {
	align-items: center;
	display: none;
}

section {
	background: #fff;
}

.pad {
	padding: 60px 0;
}

header {
	color: #fff;
	padding: 15px 0;
	position: fixed;
	width: 100%;
	z-index: 9999;
	line-height: 0;

	.container {
		line-height: 0;
	}

	&.header-background {
		background-color: rgba(255,255,255,.95);
		color: #333;
		box-shadow: 0px 0px 10px rgba(0,0,0,0.2);
		transition: background-color 0.2s ease;
		text-shadow: none;

		a {
			color: #333;
			@media #{$mid-point} {
				&:hover {
					border-bottom-color: rgba(80, 80, 80,.3);
					border-bottom-width: 5px;
				}
			}
		}

		.dark-logo {
			display: inline-flex;
		}

		.light-logo {
			display: none;
		}
	}

	a {
		color: #fff;
		text-decoration: none;
		z-index: 1;
		position: relative;
		@media #{$mid-point} {
			height: 100%;
		}
		display: block;

		span {
			display: block;
			height: 100%;
		}

		&:hover {
			text-decoration: none;
		}
	}

	.company-name {
		font-size: 1.2em;
		display: inline-block;
		letter-spacing: -1px;

		span {
			padding-left: 5px;

			@media #{$max-desktop} and #{$mid-point} {
				display: none;
			}
		}

		img {
			height: 30px;
			width: auto;
		}
	}
}

.content {
	background: #fff;
}

.info img {
	max-height: 650px;
}

.info {
	padding: 50px 0 20px 0;

	&:first-child {
		padding-top: 100px;
	}
}

.untitled-info {
	&:first-child {
		padding-top: 10px;
	}
}

.collapsed-info {
	padding: 20px 0 20px 0;

	&:first-child {
		padding-top: 50px;
	}
}

.subtext {
	margin-top: 10px;
}

.page h2 {
	text-align: center;
}

.page {
	margin-bottom: 0;
	padding-bottom: 80px;
}

.center-text {
	text-align: center;
}

.strong {
	font-weight: bold;
	font-style: italic;
}
